export default {
	data() {
		return {
			goodsNumber: 1,
			goodsPrice: 2.98,
			modalShow: false
		};
	},
	computed: {
		totalPrice: function() {
			return this.goodsNumber * this.goodsPrice;
		}
	},
	methods: {
		addGoodsNumber() {
			this.goodsNumber += 1;
		},
		deleteGoodsNumber() {
			if (this.goodsNumber <= 1) {
				this.goodsNumber = 1;
			} else {
				this.goodsNumber -= 1;
			}
		},
		showModal() {
			this.modalShow = 'show';
		},
		hideModal() {
			this.modalShow = '';
		},
		confirmOrders() {
			//本地模拟订单提交UI效果
			uni.showLoading({
				title:'正在提交订单...'
			})
			setTimeout(()=>{
				uni.setStorage({
					key:'paymentOrder',
					data:'',
					success: () => {
						uni.hideLoading();
						uni.navigateTo({
							url:"../pay/payment/payment?amount="+this.sumPrice
						})
					}
				})
			},1000)
		}
	}
};
